body {
    background-color: var(--lightGreen);
}

.navBarStyle {
    display: flex;
    flex-direction: row;
    width: 500px;
    margin: 0px auto;
    margin-top: 100px;
    border-radius: 20px;


    button {
        position: fixed;
        left: 70%;
        top: 15%;
        clip-path: circle(50% at 50% 50%);
        width: 50px;
        height: 50px;
        border: none;

    }

    div {
        display: flex;
        flex-direction: row;
        gap: 20px;
    }


}

@media screen and (max-width : 500px) {
    .navBarStyle {
        width: 300px;
    }
}

.navBarText {

    margin: 0px auto;
    display: flex;
    align-items: center;
    color: rgba(255, 255, 255, 0.781);
    font-weight: bolder;
    font-size: 2rem;
    transition: 1s;
    text-decoration: none;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);





    &:hover {
        transition: 0.3s;
        color: white;


    }

}